import React from "react"
import css from "./styles.module.scss"

const ButtonClicks = ({ onDone, clicks = 20 }) => {
  const [currentClicks, setCurrentClicks] = React.useState(0)
  const handleClick = () => {
    if (currentClicks >= clicks) return
    setCurrentClicks((c) => c + 1)
  }

  if (currentClicks >= clicks) {
    setTimeout(onDone, 1000)
  }

  return (
    <div className={css.container}>
      <div className={css.button} onClick={handleClick}>
        {currentClicks >= clicks ? (
          "GREAT!"
        ) : (
          <>
            {" "}
            <div>CLICK</div>
            <div>
              {currentClicks} / {clicks}
            </div>
          </>
        )}
      </div>
    </div>
  )
}

export default ButtonClicks
